#root,#root>div,body,html{height:50%}body{color:#454757;font-family:'Hind Guntur',sans-serif;letter-spacing:.5px}h1{color:#010000;font-family:'Hind Guntur',sans-serif;letter-spacing:.5px;font-size:24px;font-weight:700;position:absolute;top:15%}p{position:absolute;top:20%}a{text-decoration:none;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer}a:hover{text-decoration:none;color:#ff1755;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out}.container-fluid{line-height:24px}header{position:relative;display:block;width:100%;background:#010000;color:#fff;height:90px}header .logo{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header .logo img{height:50px}header nav,header nav ul,header nav ul li{margin:0;padding:0}header nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header nav ul li{list-style-type:none;display:inline-block;margin-right:20px}header nav ul li.last{margin:0}header nav ul li a{font-size:16px;line-height:normal;color:#fff}footer{position:absolute;top:88%;display:block;width:100%;background:#010000;color:#fff;height:90px}footer .footerText{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}